.container {
    height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .inputContainer h1 {
    font-family: 'Playfair Display', serif;
    font-size: 3.5rem;
    color: #fff;
    margin-bottom: 1.5rem;
  }
  .inputContainer h1 span {
    color: var(--primary-color);
  }
  .inputContainer div {
    display: flex;
    margin-bottom: 2rem;
  }
  .inputContainer input {
    width: 100%;
    padding: 0.5rem 0.2rem 0.5rem 0;
    text-indent: 1rem;
    border: none;
    outline: none;
    font-size: 1rem;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }
  .inputContainer input::placeholder {
    font-size: 0.9rem;
  }
  .inputContainer button {
    padding: 0 1rem;
    height: 3rem;
    font-size: 0.8rem;
    text-transform: uppercase;
    font-weight: bold;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    color: #fff;
    background-color: gray;
    border: none;
    cursor: pointer;
    margin-left: 1vh;
    margin-bottom: 2vh;


  }
  
  /* Result */
  .result {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.5rem 1rem;
  }
  .result p {
    color: #fff;
    border: 1px solid var(--primary-color);
    padding: 0.5rem 1rem;
    margin-right: 1rem;
  }
  .result button {
    padding: 0.5rem 1rem;
    color: #fff;
    cursor: pointer;
    background: gray;
    border: none;
    margin-bottom: 2vh;
    margin-right: 1vh;


  }
  .result button.copied {
    background: #fff;
    color: black;
  }
  .noData {
    color: #fff;
    font-size: 1rem;
  }
  
  /* background animate */
  @keyframes animate {
    0%{
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }
    100%{
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }
  }
  
  .background {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    background: #4e54c8;
    overflow: hidden;
    z-index: -1;
  }
  .background li {
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.2);
    animation: animate 19s linear infinite;
  }
  
  
  
  
